.stages {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	&__list {
		// @include adaptiv-value("margin-top",-20 , -10, 1);
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
}
.item-stages {
	display: flex;
	align-items: flex-start;
	background: var(--color-c);
	box-shadow: 3px 4px 4px #dfdfdf;
	@include adaptiv-value('padding', $offset-4, $offsetMobile-4, 1);
	width: 48%;
	@include adaptiv-value('margin-right', $offset-2, $offsetMobile-2, 1);
	@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
	cursor: pointer;

	&:hover {
		transition-duration: 0.4s !important;
		transform: scale(1.02) !important;
	}

	&:nth-child(-n + 2) {
		margin-top: 0;
	}
	&:nth-child(2n + 2) {
		margin-right: 0;
	}
	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__info-text {
		color: var(--color-a);
		font-size: $font-size-l + px;
		font-weight: 700;
	}
	&__info-numb {
		color: var(--color-a);
		font-size: $font-size-bxl + px;
		font-weight: 700;
	}
	&__text {
		margin-top: 10px;
		margin-left: 30px;
		@include adaptiv-value('font-size', $font-size-s, $font-size-xs, 1);
	}
	&__title {
		text-align: center;
		@include adaptiv-value('font-size', $font-size-l, $font-size-xs, 1);
		font-weight: 700;
		@include adaptiv-value('margin-top', $offset-1, $offsetMobile-1, 1);
	}
}
