.footer {
	min-height: 338px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px 0;
	&__copyright {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		& > *:not(:first-child) {
			@include adaptiv-value('margin-top', $offset-05, $offsetMobile-05, 1);
		}
	}
	&__copyright-title {
		color: var(--color-c);
		@include adaptiv-value('font-size', $font-size-m, $font-size-xs, 1);
		text-align: end;
	}
	&__copyright-link {
		text-align: end;
	}
	&__logo {
		max-width: 33.3%;
	}
	&__logo-picture {
		width: 185px;
		.Picture__block-img {
			padding-bottom: 71%;
		}
	}
	&__feedback {
		max-width: 33.3%;
	}
	&__social-networks {
		align-self: flex-end;
		max-width: 33.3%;
	}
	&__feedback {
		.Feedback {
			&__title {
				color: var(--color-e);
				font-weight: 700;
				font-size: 24px;
			}
			&__block-number,
			&__block-address {
				display: flex;
				align-items: flex-start;
			}
			&__block-mail {
				display: flex;
				align-items: center;
				.Link {
					&--s {
						font-size: 16px;
					}
				}
			}
			&__block-number-icon,
			&__block-address-icon,
			&__block-mail-icon {
				@include adaptiv-value('margin-right', $offset-1, $offsetMobile-1, 1);
			}
			&__number-link {
				font-weight: 700;
			}
			&__address-title {
				color: var(--color-e);
				font-weight: 400;
				@include adaptiv-value('font-size', $font-size-s, $font-size-xs, 1);
				line-height: 144%;
				max-width: 174px;
			}
			&__wrapper {
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				& > div {
					&:not(:first-child) {
						@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
					}
				}
			}
		}
	}
	&__wrapper {
		// @include adaptiv-value('padding-top', $offset-4, $offsetMobile-4, 1);
		// @include adaptiv-value('padding-bottom', $offset-4, $offsetMobile-4, 1);
		// position: relative;
		// @include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		// display: flex;
		// justify-content: space-between;
		// align-items: flex-start;
		// display: grid;
		// grid-template-columns: 1fr 1fr 1fr;
		// gap: 20px;
		// align-items: center;
		// height: 166px;
		// justify-items: center;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
	}
}
