.Hamb {
	display: none;
	&__bar {
		display: block;
		width: 40px;
		height: 4px;
		margin: 6px auto;
		background-color: var(--color-a);
		transition: 0.2s;
	}
	&__field {
		padding: 10px 20px;
		cursor: pointer;
	}

	&__field.active &__bar {
		&:nth-child(2) {
			opacity: 0;
		}
		&:nth-child(1) {
			transform: translateY(12px) rotate(45deg);
		}
		&:nth-child(3) {
			transform: translateY(-8px) rotate(-45deg);
		}
	}
}
