@media (max-width: 1650px) {
	//! Blocks
	.Card-img {
		@include adaptiv-value('height', 577, 180, 1);
		&--link {
			@include adaptiv-value('height', 710, 320, 1);
		}
	}
	//! Elements
	//! Popups
	//! Section
	//! Layouts
	//! Global
}
@media (max-width: 1350px) {
	//! Blocks
	//! Elements
	//! Popups
	//! Section

	//! Layouts
	//! Global
}
@media (max-width: 1250px) {
	//! Blocks
	.Card-b {
		&__link {
			@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
		}
	}
	.Card-img {
		&__inner {
			padding: 45px 20px;
		}
	}

	//! Elements
	//! Popups
	//! Section
	.news {
		&__content-item {
			&:nth-child(even) {
				.news {
					&__content-picture {
						order: -1;
					}
					&__content-card-b {
						order: unset;
					}
				}
				.Card-b {
					&__heading {
						text-align: left;
					}
					&__content {
						text-align: left;
					}
					&__heading-title {
						justify-content: flex-start;
					}
					&__link {
						text-align: left;
					}
				}
			}
		}
		&__heading-subtitle {
			display: none;
		}
	}
	.price {
		&__list {
			grid-template-columns: repeat(1, 1fr);
		}
	}
	.info {
		&__content {
			max-width: 100%;
			margin-right: 0;
			order: 1;
			@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
		}

		&__inner {
			flex-direction: column;
			justify-content: center;
			// align-items: center;
		}
	}
	//! Layouts
	//! Global

	[class*='--grid-d'] {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}
	[class*='--grid-i'] {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
	}
}

@media (max-width: 1023px) {
	//! Blocks
	.detail {
		&__info-wrapper {
			// flex-direction: column;
		}
		&__info-desc {
			order: -1;
		}
		&__info-status {
			@include adaptiv-value('margin-top', $offset-7, $offsetMobile-7, 1);
		}
		&__info-status-title {
			justify-content: center;
			text-align: center;
		}
	}
	.Tabs {
		&--vertical {
			.Tabs {
				&__btns {
					margin-right: 0;
					max-height: 100%;
					overflow-y: auto;
					&--active {
						max-height: 200px;
					}
					.Button--theme-m-w {
						width: 100%;
					}
				}
				&__block {
					display: none;
				}
				&__blocks {
					// display: none;
				}
				&__blocks-wrapper {
					@include adaptiv-value('margin-top', $offset-7, $offsetMobile-7, 1);
				}
				&__item {
					width: 100%;
					// &--active {
					// 	order: -1;
					// }
				}
				&__wrapper {
					flex-direction: column;
				}
			}
		}
	}
	//! Elements
	.Button {
		&--m {
			width: 100%;
		}
	}
	//! Popups
	//! Section
	.choice {
		&__list {
			align-items: flex-start;
		}
		&__item--col {
			@include adaptiv-value('min-height', 300, 200, 1);
		}
		&__item-bottom {
			.item-choice__picture {
				// min-width: 80px;
				// min-height: 50px;
			}
			.item-choice__content-text {
				margin-left: 0;
			}
		}
	}

	.hero {
		background-image: url(/img/hero-bg-mobile.jpg) !important;
		// min-height: 620px;
		height: calc(100vh - 93px);
		text-align: center;
		&__img {
			display: none;
		}
		&__wrapper {
		}
		&__desc {
			width: 100%;
			margin-right: 0;
			.Button {
				&--m {
					width: 100%;
				}
			}
		}
	}
	.footer {
		background-image: url(/img/footer-bg-mobile.jpg) !important;
	}
	.delivery {
		background: url('../img/delivery-bg-mobile.jpg') no-repeat;
		background-size: cover;
		background-position: center;
	}
	.management {
		&__list {
		}
	}
	.ability {
		.Tabs {
			@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
			&__btns {
				justify-content: flex-start;
				.Button {
					&__text {
					}
				}
			}
			.block-tabs {
				&__content {
					margin-left: 0;
					@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
					max-width: 100%;
				}
				&__content-title {
				}
				&__content-text {
				}
				&__img {
					max-width: 100%;
					.Picture {
						&__block-img {
						}
					}
				}
				&__wrapper {
					flex-direction: column;
				}
			}
		}
	}

	.integration {
		&__wrapper {
			flex-direction: column;
		}
		&__text {
			max-width: 100%;
			margin-right: 0;
			text-align: center;
		}
		.Button {
			@include adaptiv-value('margin-top', $offset-6, $offsetMobile-6, 1);
		}
	}
	.item-management,
	.item-stages {
		width: 100%;
		margin-top: 20px;
		margin-right: 0;
		min-height: 100%;

		&:nth-child(-n + 3) {
			margin-top: 20px;
		}
		&:nth-child(3n + 3) {
			margin-right: unset;
		}
	}
	.item-choice {
		@include adaptiv-value('max-height', 120, 60, 1);
		@include adaptiv-value('max-height', 120, 60, 1);
		&__content {
			flex-direction: row;
			align-items: center;
			max-width: 100%;
			max-height: 100%;
			.Icon--color-e .Icon__svg {
				fill: var(--color-a);
			}
		}
		&__content-text {
			margin-top: 0;
			@include adaptiv-value('margin-left', $offset-1, $offsetMobile-1, 1);
			color: var(--color-b);
		}
		.Picture {
			&__img {
				display: none;
			}
		}
	}
	//! Layouts
	.header {
		&__contacts {
			justify-content: flex-start;
			border-top: 1px solid var(--color-c);
		}
		&__info {
			flex-direction: column;
		}
		&__nav {
			justify-content: flex-start;
		}
	}
	.footer {
	}
	//! Global
}
@media (max-width: 866px) {
	//! Blocks
	.Hamb {
		display: flex;
		align-items: flex-start;
	}
	.Nav {
		&__list {
			display: none;
			.item-nav {
				&--mob {
					display: block;
				}
				text-transform: uppercase;
				font-weight: 900;
				text-align: center;
				&:not(:last-child) {
					margin-right: 0;
				}
			}
		}
	}
	.Card-a {
		&__wrapper {
			// min-height: 100vh;
		}
	}
	.up {
		display: none;
	}
	.Card-c {
		&__text {
			display: none;
		}
	}
	//! Elements
	.choice__item-middle .Icon--l .Icon__svg {
		height: 50px;
		width: 50px;
	}
	//! Popups
	//! Section
	.hero {
		&__mobile {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
		&__swiper-container {
			display: none;
		}
	}
	.contacts {
		&__top {
			flex-direction: column;
		}
		&__top-left {
			align-items: center;
			margin-right: 0;
		}
		&__top-right {
			align-items: center;
		}
		&__info-city-title {
			text-align: center;
			justify-content: center;
		}
		&__info-city-time-text {
			text-align: center;
		}
		&__feedback-phone {
			display: none;
		}
		&__social-networks {
			display: none;
		}
		&__feedback-mail {
			@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
		}
		&__main-map {
			display: none;
		}
		&__main-actions {
			margin-right: 0;
			.Button {
				&:not(:first-child) {
					margin-top: 20px;
				}
			}
		}
		&__main-actions-text {
			display: none;
		}
	}
	.about-us {
		&__item-title {
			display: none;
		}
		&__list {
		}
		&__item {
			&:not(:nth-child(2)) {
				display: none;
			}
		}
	}
	.services {
		&__list {
			.item-services {
				&__content-desc {
					display: none;
				}
				&__wrapper {
					align-items: center;
				}
			}
		}
	}
	//! Layouts
	.header {
		&--main {
			// display: none;
		}
		&__btn {
			display: none;
		}
		&__action {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}
		&__wrapper {
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		&__navigation {
			max-width: 100%;
		}
	}
	.footer {
		&__wrapper {
			flex-direction: column;
			align-items: center;
			justify-content: center;
			& > div {
				@include adaptiv-value('margin-top', $offset-8, $offsetMobile-8, 1);
			}
		}

		&__social-networks {
			align-self: center;
			order: 3;
			.Social-networks {
				&__list {
					margin-top: 18px;
				}
				&__title {
					order: -1;
				}
			}
		}
		&__feedback {
			.Icon__content {
				display: none;
			}
			.Feedback {
				&__wrapper {
					align-items: center;
				}
				&__address-title {
					text-align: center;
				}
			}
		}
		&__logo {
			justify-content: center;
		}
		&__logo,
		&__social-networks,
		&__feedback {
			max-width: 100%;
		}
	}
	//! Global
	[class*='--grid-d'] {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}
	[class*='--grid-e'] {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}
	[class*='--grid-g'] {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}
}

@media (max-width: 733px) {
	//! Blocks
	.new {
		&__heading-nav {
			@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
		}
	}
	.Tabs {
		&--vertical {
			.Tabs {
				&__blocks-wrapper {
					grid-template-columns: repeat(1, 1fr);
				}
				&__blocks-title {
					@include adaptiv-value('margin-bottom', $offset-3, $offsetMobile-3, 1);
				}

				&__blocks-list {
					box-shadow: unset;
					padding: 0;
				}
			}
		}
	}
	//! Elements
	.choice {
		&__item-bottom {
			.item-choice__content-text {
				font-size: 12px !important;
			}
			.item-choice {
				&:nth-child(1),
				&:nth-child(2) {
					width: 106px;
				}

				&__picture {
					width: 106px;
				}
			}
		}
	}
	//! Popups
	//! Section
	.advantage {
		&__list {
			grid-template-columns: repeat(1, 1fr);
		}
	}
	.management {
		&__benefit-list {
			grid-template-columns: repeat(1, 1fr);
			gap: 20px;
		}
	}

	.item-management {
		&__title {
			font-size: 18px;
			br {
				display: none;
			}
		}
		&__text {
			margin-top: 12px;
			font-size: 14px;
		}
	}
	.item-advantage {
		&__text {
			font-size: 18px;
			margin-left: 10px;
		}
		.Icon--m {
			.Icon__svg {
				width: 24px;
				height: 24px;
			}
		}
	}
	.item-benefit-management {
		&__title {
			min-height: max-content;
			br {
				display: none;
			}
		}
	}
	//! Layouts
	.footer {
		&__copyright-title {
			text-align: start;
			width: 100%;
		}
		&__copyright-link {
			text-align: start;
			width: 100%;
		}
		&__wrapper {
			grid-template-columns: 1fr;
			justify-items: start;
		}
	}
	//! Global
}

@media (max-width: 650px) {
	//! Blocks
	//! Elements
	.choice {
		&__item-bottom {
			.item-choice {
				&:nth-child(1) {
					// @include adaptiv-value('left', -20, -80, 1);
				}
				&:nth-child(2) {
					@include adaptiv-value('left', 140, -10, 1);
				}
			}
		}
		&__item {
			width: 33% !important;
		}
		&__list {
		}
		.item-choice {
			&__content {
				.Icon--l .Icon__svg {
					width: 30px;
					height: 30px;
				}
			}
			&__content-text {
				font-size: 16px;
			}
		}
	}
	//! Popups
	//! Section

	//! Layouts
	//! Global
}

@media (max-width: 500px) {
	//! Blocks
	.Card-d {
		// &__wrapper:hover > &__content {
		// 	height: 100%;
		// }
		&__content {
			height: 100%;
			display: flex;
			justify-content: center;
			flex-direction: column;
			padding: 5px;
		}
		&__content-title {
			font-size: 11px;
		}
		&__content-subtitle {
			font-size: 10px;
		}
	}
	.Social-networks {
		br {
			display: unset;
		}
		&__title {
			text-align: center;
		}
	}
	//! Elements

	//! Popups
	//! Section
	.item-choice__picture .Picture__block-img {
		padding-bottom: 30%;
	}
	.integration {
		padding: 0 15px;
	}
	//! Layouts
	.footer {
		&__info-phone-list {
			align-items: flex-start;
		}
		&__info-phone-item {
			&:last-child {
				display: none;
			}
		}
		&__logo {
			margin: 0;
			// margin-bottom: 30px;
			display: flex;
			width: 100%;
			.Picture {
				width: 30%;
			}
		}
		&__wrapper {
			flex-direction: column;
		}
	}
	//! Global
	[class*='--grid-b'] {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
	}
	[class*='--grid-d'] {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}
}
