	//! Animation
	&--animation-scale {
		.Icon {
			&__svg {
				animation-name: scale;
				animation-duration: 1.3s;
				animation-timing-function: linear;
				animation-iteration-count: infinite;
				animation-direction: alternate;
			}
		}

		/* ключевые кадры */
		@keyframes scale {
			0% {
				transform: scale(1);
			}
			50% {
				transform: scale(1.3);
			}
			100% {
				transform: scale(1);
			}
		}
	}
}
