.ability {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	.Tabs {
		@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
		&__item {
			padding-bottom: 10px;
		}
		&__btns {
			overflow-x: auto;
			&::-webkit-scrollbar {
				width: 3px ; /* ширина для вертикального скролла */
				height: 3px ; /* высота для горизонтального скролла */
				background-color: transparent;
			}
			&::-webkit-scrollbar-thumb {
				// border-radius: 9em;
				// box-shadow: inset 1px 1px 6px #f3faf7;
			}
			.Button {
				&__text {
					white-space: nowrap;
				}
			}
		}
		.block-tabs {
			&__content {
				max-width: 555px;
				width: 100%;
				@include adaptiv-value('margin-left', $offset-3, $offsetMobile-3, 1);
			}
			&__content-list {
				list-style: disc;
				@include adaptiv-value('font-size', $font-size-m, $font-size-xs, 1);
				line-height: 144%;
				@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
			}
			&__content-item {
				@include adaptiv-value('margin-left', $offset-4, $offsetMobile-4, 1);
			}
			&__content-title {
				font-weight: 700;
				font-size: 29px;
			}
			&__content-text {
				@include adaptiv-value('font-size', $font-size-m, $font-size-xs, 1);
				line-height: 144%;
				@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
			}
			&__img {
				max-width: 600px;
				width: 100%;
				filter: drop-shadow(3px 4px 4px #dfdfdf);
				.Picture {
					&__block-img {
						padding-bottom: 61%;
					}
				}
			}
			&__wrapper {
				display: flex;
				justify-content: space-between;
				@include adaptiv-value('margin-top', $offset-5, $offsetMobile-5, 1);
			}
		}
	}
}
