.bid {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	&__btn {
		display: flex;
		justify-content: center;
		width: 100%;
		@include adaptiv-value('margin-top', $offset-5, $offsetMobile-5, 1);
	}
	&__subtitle {
		display: flex;

		justify-content: center;
		text-align: center;
	}
	&__subtitle {
		@include adaptiv-value('font-size', $font-size-l, $font-size-xs, 1);
		color: var(--color-e);
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		text-align: center;
	}
	&__heading-title {
		text-align: center;
	}
	&__wrapper {
		&--container {
		}
		&--max-width {
		}
	}
}
