.advantage {
	@include adaptiv-value("padding-top", $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value("padding-bottom", $offset-7, $offsetMobile-7, 1);
	&__item {
	}
	&__list {
		@include adaptiv-value('margin-top', $offset-5, $offsetMobile-5, 1);
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		@include adaptiv-value('gap', $offset-6, $offsetMobile-6, 1);
	}
	&__wrapper {
		&--container {
		}
		&--max-width {
		}
	}
}
.item-advantage {
	display: flex;
	align-items: flex-start;
	&__text {
		font-size: 22px;
		@include adaptiv-value('margin-left', $offset-1, $offsetMobile-1, 1);
		line-height: 133%;
	}
}
