.Nav {
	&__link {
	}
	&__nav {
		display: flex;
		justify-content: center;
	}

	&__list {
		display: flex;
		justify-content: space-between;
		.item-nav {
			&__link {
				text-transform: none;
				&--active {
					color: var(--color-a);
					// text-shadow: 0 0 0.45px var(--color-a), 0 0 0.45px var(--color-a);
				}
			}
			&--mob {
				display: none;
			}
			font-weight: 500;
			&:not(:last-child) {
				@include adaptiv-value('margin-right', $offset-3, $offsetMobile-3, 1);
			}
		}
	}
	&__item {
	}
	&__wrapper {
	}
	&__mobile {
		position: fixed;
		// @include adaptiv-value('top', 150, 130, 1);
		top: 80px;
		left: -100%;
		width: 100%;
		height: calc(100vh - 93px);
		background-color: #fff;
		z-index: 100;
		display: flex;
		transition: 0.3s;
		flex-direction: column;
		&-wrapper {
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 20px 15px 40px 15px;
		}
		&-social-networks {
			width: 100%;
			margin-top: 25px;
		}
		&.open {
			left: 0;
		}
		.Nav__list {
			order: -1;
			width: 100%;
			max-height: 55vh;
			// height: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			padding: 15px;
			overflow: auto;
			& > li {
				width: 100%;
				& > a {
					width: 100%;
					display: flex;
					justify-content: center;
					padding: 20px 0;
					font-size: 20px;
					font-weight: bold;
					color: #3f3f3f;
					&:hover {
						background-color: rgba(122, 82, 179, 0.1);
					}
				}
			}
		}
	}
}
