.choice {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	&__list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;
		width: 100%;
		@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
	}
	&__item {
		height: 100%;
		width: 100%;

		&--col {
			flex-direction: column;
			align-items: center;
			justify-content: space-between;
			display: flex;
			min-height: 600px;
		}
	}
	&__item-top {
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		.item-choice {
			position: relative;
			&::after {
				position: absolute;
				
			}
		}
	}
	&__item-middle {
		position: relative;
		display: flex;
		top: 20px;
		.item-choice__item-middle-icon {
			&:not(:last-child) {
				@include adaptiv-value("margin-right", $offset-10, $offsetMobile-10, 1);
			}
		}
		.Icon--l .Icon__svg {
			@include adaptiv-value("width", 150, -152, 1);
			@include adaptiv-value("height", 150, -152, 1);

		}
	}
	&__item-bottom {
		display: flex;
		justify-content: space-between;
		height: 100%;
		width: 100%;
		font-weight: 400;
		.item-choice {
			max-width: 150px;
			max-height: 180px;
			position: relative;
			&:nth-child(1) {
				@include adaptiv-value("left", -20, -100, 1);
				// width: 100px;
			}
			&:nth-child(2) {
				@include adaptiv-value("left", 20, 80, 1);
				// width: 100px;

			}
			&__content {

			}
			&__content-text {
				font-weight: 400;
				font-size: 20px;
				text-align: center;
				margin-top: 0;
			}
			&__picture .Picture__block-img {
				padding-bottom: 120%;
			}
		}
	}
}
.item-choice {
	position: relative;
	max-width: 280px;
	max-height: 280px;
	height: 100%;
	width: 100%;
	&__content {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 160px;
		max-height: 130px;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		overflow: hidden;
	}
	&__content-text {
		@include adaptiv-value('font-size', $font-size-xxl, $font-size-m, 1);
		color: var(--color-e);
		font-weight: 700;
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
	}
	&__picture {
		max-width: 280px;
		max-height: 280px;
		.Picture__block-img {
			padding-bottom: 100%;
			filter: drop-shadow(10px 0px 0px #e42320);
		}
	}
}
