.Link {
	text-decoration-line: none;
	transition: $transition-time;
	cursor: pointer;
	$blockName: &;
	//! Общие стили для всех кнопок (для каждого проекта свои стили)
	color: var(--color-b);
	$colorDecor: var(--color-b);
	&:hover {
		color: var(--color-b);
		opacity: 0.8;
	}
	&:disabled {
		color: rgb(0, 0, 0);
		cursor: initial;
	}
	// !Color
	&--color-a {
		$colorAccent: var(--color-a);
		color: $colorAccent;
		&:not(:disabled) {
			&:hover,
			&:focus {
				color: $colorAccent;
				opacity: 0.7;
			}
		}
		&-decor {
			position: relative;
			color: $colorAccent;
		}
		&-decor::before {
			content: '';
			position: absolute;
			top: 100%;
			display: block;
			width: 100%;
			height: 1px;
			background: $colorAccent;
			transform: scale(0);
			transition: $transition-time;
		}
		&-decor:hover::before {
			transform: scale(1);
			transition: $transition-time;
		}
	}
	&--color-b {
