	// !Size
	&--xs {
		font-size: $font-size-xs + px;
	}
	&--s {
		@include adaptiv-value("font-size", $font-size-m, 8, 1);
	}
	&--m {
		@include adaptiv-value('font-size', $font-size-m, $font-size-s, 1);
	}
	&--l {
		@include adaptiv-value('font-size', $font-size-l, $font-size-s, 1);
	}
		&--xl {
