.item-management {
	display: flex;
	flex-direction: column;
	align-items: center;
	background: var(--color-c);
	box-shadow: 3px 4px 4px #dfdfdf;
	@include adaptiv-value('padding', $offset-4, $offsetMobile-4, 1);
	width: 32.1%;
	@include adaptiv-value('margin-right', $offset-2, $offsetMobile-2, 1);
	@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
	min-height: 300px;
	&:nth-child(-n + 3) {
		margin-top: 0;
	}
	&:nth-child(3n + 3) {
		margin-right: 0;
	}
	&__text {
		text-align: center;
		@include adaptiv-value('margin-top', $offset-1, $offsetMobile-1, 1);
	}
	&__title {
		text-align: center;
		@include adaptiv-value('font-size', $font-size-l, $font-size-xs, 1);
		font-weight: 700;
		max-width: 220px;
		@include adaptiv-value('margin-top', $offset-1, $offsetMobile-1, 1);
	}
}
.item-benefit-management {
	padding: 12px;
	&__title {
		font-size: 18px;
		font-weight: 700;
		line-height: 133%;
		border-bottom: 2px solid var(--color-a);
		text-align: center;
		@include adaptiv-value('padding-bottom', $offset-05, $offsetMobile-05, 1);
		min-height: 79px;
		
	}
	&__subtitle {
		@include adaptiv-value('margin-top', $offset-05, $offsetMobile-05, 1);
		text-align: center;
		line-height: 144%;
	}
}
.management {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	&__item {
	}
	&__list {
		// @include adaptiv-value("margin-top",-20 , -10, 1);
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	&__benefit {
		@include adaptiv-value('margin-top', $offset-7, $offsetMobile-7, 1);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	&__benefit-list {
		max-width: 860px;
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		@include adaptiv-value('gap', $offset-4, $offsetMobile-4, 1);
	}
	&__benefit-title {
	}
	&__heading-title {
		text-align: center;
	}
	&__wrapper {
		&--container {
		}
		&--max-width {
		}
	}
}
