.price {
	background: #f8f8f8;
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value("margin-top", $offset-1, $offsetMobile-1, 1);
	&__item {
	}
	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 20px;
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
	}

	&__wrapper {
		&--container {
		}
		&--max-width {
		}
	}
}

.item-price {
	// @include adaptiv-value('padding-top', $offset-4, $offsetMobile-4, 1);
	// @include adaptiv-value('padding-bottom', $offset-5, $offsetMobile-5, 1);
	// @include adaptiv-value('padding-left', $offset-5, $offsetMobile-5, 1);
	// @include adaptiv-value('padding-right', $offset-5, $offsetMobile-5, 1);
	background: #ffffff;
	box-shadow: 3px 4px 4px #dfdfdf;
	&__top {
		background: var(--color-h);
		position: relative;
		&::before {
			content: '';
			position: absolute;
			bottom: 0px;
			height: 34px;
			width: 100%;
			background-image: url('../../../img/Rectangle.png');
			background-repeat: no-repeat;
		}
	}
	&__top-wrap {
		@include adaptiv-value('padding-top', $offset-4, $offsetMobile-4, 1);
		@include adaptiv-value('padding-left', $offset-4, $offsetMobile-5, 1);
		@include adaptiv-value('padding-right', $offset-4, $offsetMobile-5, 1);
		@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-10, 1);
	}
	&__bottom {
		@include adaptiv-value('padding-top', $offset-4, $offsetMobile-4, 1);
		@include adaptiv-value('padding-left', $offset-4, $offsetMobile-5, 1);
		@include adaptiv-value('padding-right', $offset-4, $offsetMobile-5, 1);
		@include adaptiv-value('padding-bottom', $offset-5, $offsetMobile-5, 1);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: relative;
		@include adaptiv-value('min-height', 440, 300, 1);
		min-height: 440px;

		.Button {
			@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		}
	}
	&__bottom-btn {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__bottom-item {
		display: flex;
		align-items: flex-start;
		&:not(:first-child) {
			@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
		}
		.Icon--m .Icon__svg {
			width: 16px;
			height: 16px;
		}
	}
	&__text {
		@include adaptiv-value('margin-left', $offset-1, $offsetMobile-1, 1);
		@include adaptiv-value('font-size', $font-size-m, $font-size-xs, 1);
	}
	&__title {
		font-weight: 700;
		@include adaptiv-value('font-size', 28, 20, 1);
	}
	&__top {
	}
	&__price {
		font-weight: 700;
		@include adaptiv-value('font-size', $font-size-xxxl, $font-size-xl, 1);
		color: var(--color-a);
		@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
	}
	&__subtitle {
		@include adaptiv-value('font-size', $font-size-m, $font-size-xs, 1);
		@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
	}
}
