.Nav-link {
	position: absolute;
	bottom: -30px;
	left: 2px;
	transition: 0.3s all;
	max-width: 514px;

	&--a {
		display: flex;
		align-items: center;
		.Nav-link {
			&__nav-list {
				width: 530px;
			}
			&__nav-item {
				&::first-letter {
					text-transform: uppercase;
				}
			}
		}
	}
	&__link {
	}
	&__nav {
		display: flex;
		justify-content: center;
	}
	&__nav-item {
		white-space: nowrap;
		&--active {
			// display: none;
			.Link {
				color: var(--color-b);
				&--color-b-decor {
					position: relative;
					&::before {
						content: '';
						position: absolute;
						top: 125%;
						display: block;
						width: 100%;
						height: 2px;
						background: var(--color-b);
						transform: scale(1);
						transition: $transition-time;
					}
				}
			}
		}
		.Link {
			&--color-b-decor:hover::before {
				transform: scale(1);
			}
			&--color-b-decor::before {
				top: 125%;
				display: block;
				width: 100%;
				height: 2px;
			}
		}
		&:not(:last-child) {
			margin-right: 17px;
		}
	}
	&__nav-item-link {
		font-weight: 400;
	}
	&__nav-list {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		width: 100%;
		height: 35px;
	}

	&__wrapper {
	}
}
