.Button {
	$blockName: &;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	border: 0;
	padding: 0;
	font-family: inherit;
	font-weight: inherit;
	line-height: inherit;
	text-align: center;
	text-decoration-line: none;
	text-overflow: ellipsis;
	background-color: #fff;
	transition: $transition-time fill, $transition-time color, $transition-time background-color, $transition-time opacity;
	cursor: pointer;
	user-select: none;
	&__icon {
		display: flex;
		transition: $transition-time fill, $transition-time color, $transition-time background-color, $transition-time opacity;
	}

	&__text {
		text-overflow: ellipsis;
		transition: $transition-time fill, $transition-time color, $transition-time background-color, $transition-time opacity;
	}

	//! Общие стили для всех кнопок (для каждого проекта свои стили)
	font-size: 16px;
	padding: 5px 5px;
	font-weight: 500;
	@include adaptiv-value('min-height', 46, 44, 1);
	border-radius: 5px;
	// box-shadow: 0px 5px 15px rgba(75, 81, 104, 0.5);
	// text-transform: uppercase;
	&__icon-svg {
		margin-right: 10px;
		width: 24px;
		height: 24px;
	}

	// !Color
	// ? Accent//

	&--color-a {
		$colorAccent: var(--color-e);
		$colorAccentHover: var(--color-a);

		border: 1px solid $colorAccentHover;
		background-color: $colorAccentHover;
		transition: $transition-time fill, $transition-time color, $transition-time background-color, $transition-time opacity;

		#{$blockName}__icon-svg {
			fill: $colorAccent;
			transition: $transition-time fill, $transition-time color, $transition-time background-color, $transition-time opacity;
		}

		#{ $blockName }__text {
			color: $colorAccent;
		}

		&:hover {
			outline: none;
			box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
			color: $colorAccentHover;
			background: $colorAccent;
		}

		&:hover #{ $blockName }__text {
			color: $colorAccentHover;
		}

		&:hover > #{ $blockName }__icon #{ $blockName }__icon-svg {
			fill: $colorAccentHover;
		}

		&:disabled {
			border-color: #e5e5e5;
			background-color: #f7f6fa;
			cursor: initial;

			#{ $blockName }__text {
				color: #888;
			}
		}
	}
