.info {
	@include adaptiv-value('padding-top', $offset-7, $offsetMobile-7, 1);
	@include adaptiv-value('padding-bottom', $offset-7, $offsetMobile-7, 1);
	&__content {
		max-width: 800px;
		@include adaptiv-value('margin-right', $offset-5, $offsetMobile-5, 1);
	}
	&__content-text {
		@include adaptiv-value('font-size', $font-size-l, $font-size-xs, 1);
		&:not(:first-child) {
			@include adaptiv-value('margin-top', $offset-5, $offsetMobile-5, 1);
		}
	}
	&__desc {
		max-width: 330px;
	}
	&__desc-list {
	}
	&__desc-list-item {
		display: flex;
		align-items: center;
		&:not(:first-child) {
			@include adaptiv-value('margin-top', $offset-5, $offsetMobile-5, 1);
		}
	}
	&__desc-list-text {
		@include adaptiv-value('margin-left', $offset-2, $offsetMobile-2, 1);
		@include adaptiv-value('font-size', $font-size-l, $font-size-m, 1);
		color: var(--color-a);
		font-weight: 700;
	}
	&__inner {
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
		display: flex;
		justify-content: space-between;
	}
	&__wrapper {
		&--container {
		}
		&--max-width {
		}
	}
}
