.Social-networks {

	&--header {
		.Social-networks {
			&__list {
				justify-content: space-evenly;
			}
		}
	}
	&--Card-a {
		.Social-networks {
			&__list {
				justify-content: space-evenly;
			}
		}
	}
	&__content {
		display: flex;
		align-items: center;
	}
	&__icon {
		cursor: pointer;
		&:hover {
			filter: contrast(1.2);
		}
	}
	&__item {
		display: flex;
		align-items: center;
		&:not(:last-child) {
			@include adaptiv-value('margin-right', $offset-4, $offsetMobile-4, 1);
		}
	}

	&__title {
		a {
			color: var(--color-e);
			pointer-events: none;
		}
		color: var(--color-e);
		line-height: 144%;
		@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
		br {
			display: none;
		}
		span {
			color: var(--color-e);
		}
	}
	&__list {
		display: flex;
		align-items: flex-end;
		justify-content: center;
		height: 100%;
	}
	&__link {
		display: flex;
		align-items: center;
		.Icon--l .Icon__svg {
			@include adaptiv-value('height', 48, 32, 1);
			@include adaptiv-value('width', 48, 32, 1);
		}
	}
	&__link-icon {


		&:hover {
			opacity: 0.7;
		}
	}
	&__wrapper {
		height: 100%;
		display: flex;
		flex-direction: column;
	}
}
