.Tabs {
	position: relative;
	width: 100%;

	&--vertical {
		.Tabs {
			&__block {
				&--vertical {
					.Tabs {
						&__block-inner {
							display: flex;
							.Banner {
								flex: 1 1 40%;
								margin-right: 40px;
								&__block-img {
									@include adaptiv-value('height', 420, 140, 1);
								}
							}
						}
						&__block-content {
							flex: 1 1 55%;
							height: 100%;
						}
					}
				}
				&-btns {
					margin-top: 40px;
					justify-content: flex-start;
					.Button {
						&--theme-m-w {
							width: auto;
							max-width: 355px;
							min-height: 60px;
							.Button {
								&__text {
									font-size: 16px;
								}
							}
						}
					}
				}
				&-desc {
					margin-top: 30px;
					a {
						text-decoration: underline;
						color: var(--color-a);
					}
					p {
						@include adaptiv-value('font-size', 18, 14, 1);
						color: var(--color-b);
						margin-top: 20px;
						&::first-letter {
							margin-left: 20px;
						}
					}
				}
				&-wrapper {
					.Banner {
						&__img {
							border-radius: 10px;
						}
					}
				}
			}
			&__blocks-wrapper {
				@include adaptiv-value('gap', $offset-6, $offsetMobile-6, 1);
				display: grid;
				grid-template-columns: repeat(2, 1fr);
			}
			&__blocks-list {
				@include adaptiv-value('margin-top', $offset-1, $offsetMobile-1, 1);
				@include adaptiv-value('padding', 20, 10, 1);
				box-shadow: 5.68187px 9.46979px 17.9926px 5.68187px #01ac9a;
			}
			&__blocks-title {
				text-transform: none;
			}
			&__blocks-item {
				@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
			}
			&__btns {
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				margin-right: 50px;
				.Button {
					justify-content: start;
					min-height: 60px;
					padding: 20px;
					&--theme-m-w {
						.Button {
							&__text {
								@include adaptiv-value('font-size', $font-size-s, $font-size-xs, 1);
								text-align: left;
								text-transform: none;
							}
						}
						width: 340px;
					}
				}
			}
			&__item {
				&:not(:last-child) {
					margin: 0;
				}
				&:not(:first-child) {
					// margin-top: 20px;
				}
				&:nth-child(4) {
					// margin-top: 40px;
				}
				.Button {
					&__text {
					}
				}
			}
			&__wrapper {
				display: flex;
			}
			&__blocks {
				flex: 1 1 70%;
				.Banner {
					margin-top: 30px;
					&__block-img {
						padding-bottom: 31%;
					}
				}
			}
			&__text {
				margin-top: 50px;
			}
		}
	}
	&__table {
		@include adaptiv-value('margin-top', 60, 40, 1);
		.Title {
			margin-bottom: 40px;
		}
	}
	&__links {
		.Tabs {
			&__link {
			}
			&__nav {
				display: flex;
				justify-content: center;
			}
			&__nav-item {
				&--active {
					.Tabs {
						&__link {
							position: relative;
						}
						&__link::before {
							content: '';
							position: absolute;
							top: 150%;
							display: block;
							width: 100%;
							height: 2px;
							background: var(--color-a);
							transform: scale(0);
							transition: 0.3s all;
							transform: scale(1.6);
							transition: 0.3s all;
						}
					}
					.Link {
						&--black-decor {
							color: var(--color-a);
						}
					}
				}
				&:not(:last-child) {
					@include adaptiv-value('margin-right', 93, 20, 1);
					margin-right: 93px;
				}
			}
			&__nav-list {
				display: inline-flex;
				align-items: center;
				overflow: scroll;
				white-space: nowrap;
				text-overflow: ellipsis;
				@include adaptiv-value('padding-bottom', 15, 15, 1);
				@include adaptiv-value('padding-left', 50, 0, 1);
				@include adaptiv-value('padding-right', 50, 0, 1);
			}
			&__wrapper-links {
				::-webkit-scrollbar {
					width: 0px; /* ширина для вертикального скролла */
					height: 0px; /* высота для горизонтального скролла */
					background-color: transparent;
				}
			}
		}
	}
	&__item,
	&__item-grid {
		transition: 0.3s all;
		cursor: auto;

		&:not(.Tabs__item--active):hover {
			cursor: pointer;
		}
	}
	&__item {
	.Button {
		&__text {
			color: var(--color-b);
		}
	}
	}
	&__item--active,
	&__item-grid--active {
		transition: 0.3s all;

		.Button {
			outline: none;

			&--line {
				border-color: transparent;
				outline: none;
				text-shadow: 0 0 0.45px #000, 0 0 0.45px #000;
				border-bottom: 1px solid var(--color-a);
				.Button {
					&__icon-svg {
						fill: var(--color-b);
					}

					&__text {
						color: var(--color-b);
					}
				}
			}
		}
	}

	&__btns {
		display: flex;
		justify-content: center;
		align-items: center;
		.Button {
			// text-transform: uppercase;
		}
	}
	&__item {
		border-radius: 6px;

		&:not(:last-child) {
			margin-right: 20px;
		}
	}

	&__block-grid {
		&--media {
			grid-template-columns: repeat(4, 1fr);
		}
	}
	&__block,
	&__block-grid {
		animation-duration: 0.4s;
		animation-fill-mode: forwards;
	}

	&__block--active,
	&__block-grid--active {
		display: block;
		animation-name: tabActive;
	}

	&__block--not-active,
	&__block-grid--not-active {
		animation-name: tabNotActive;
	}
	&__block-list {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
		@include adaptiv-value('gap', 20, 10, 1);
		@include adaptiv-value('margin-top', 30, 20, 1);
	}
	&__block-btns {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		@include adaptiv-value('margin-top', 30, 15, 1);
	}
	&__block-title {
		.Title {
			&__wrapper {
				display: flex;
				@include adaptiv-value('margin-top', 45, 20, 1);
			}
			&__text {
				@include adaptiv-value('font-size', 22, 16, 1);
				margin-right: 5px;
			}
			&__decor {
				color: var(--color-f);
				font-size: 16px;
			}
		}
	}
	@keyframes tabActive {
		0% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	@keyframes tabNotActive {
		0% {
			opacity: 1;
		}

		100% {
			opacity: 0;
		}
	}
	&__wrapper {
	}
}
