.popup-write-us {
	padding: 0;
	background: var(--color-e);

	&__wrapper {
		@include adaptiv-value('padding-top', 60, 40, 1);
		@include adaptiv-value('padding-bottom', 60, 40, 1);
		@include adaptiv-value('padding-left', 40, 20, 1);
		@include adaptiv-value('padding-right', 40, 20, 1);
		background: #ffffff;
		box-shadow: 0px 0px 20px -3px rgba(42, 35, 35, 0.51);
		border-radius: 5px;
		max-width: 500px;
	}

	&__block-input-form {
	}

	&__block-inputs {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		width: 100%;
	}
	&__bg-img {
		position: relative;

		.Picture {
			width: 150px;
			height: 100px;
			position: absolute;
			bottom: -30px;
			left: -20px;
			pointer-events: none;
			&__block-img {
				padding-bottom: 65%;
			}
		}
	}
	&__btns {
		@include adaptiv-value('margin-top', $offset-4, $offsetMobile-4, 1);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.Button--full {
			width: 200px;
		}
	}
	&__status {
		margin-top: 10px;
	}
	&__form {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include adaptiv-value('margin-top', $offset-3, $offsetMobile-3, 1);
	}
	&__subtitle {
		@include adaptiv-value('font-size', 16, 13, 1);
		text-align: center;
		font-weight: 400;
		@include adaptiv-value('margin-top', $offset-1, $offsetMobile-1, 1);
		&-text {
			display: flex;
			position: relative;
			align-items: center;
			justify-content: center;
			width: 100%;
			&::after,
			&::before {
				width: 10%;
				content: '';
				height: 1px;
				display: block;
				background: var(--color-b);
				margin: 0 10px;
			}
		}
	}
	&__heading {
	}
	&__title {
		@include adaptiv-value('font-size', 30, 18, 1);
		font-weight: 700;
		text-align: center;
	}
	.Action-button {
		margin-top: 20px;
	}
	.Button {
		// @include adaptiv-value('margin-top', 50, 40, 1);

		&__text {
		}

		&--blue {
		}

		&--theme-s {
		}
	}

	.Input {
		width: 100%;
		&__inp {
			width: 100%;

			display: flex;
			flex-shrink: 0;
		}

		&__label {
		}

		&__label-text {
			@include adaptiv-value('font-size', 18, 14, 1);

			color: var(--color-b);
		}
	}

	.Title {
		font-weight: 600;
	}

	.checkbox {
		@include adaptiv-value('margin-top', 20, 35, 1);
	}

	.input-validation {
		&__message {
		}

		&__message-mod {
		}
	}
	&__status {
		&--error {
			color: var(--color-a);
		}
		&--success {
			color: #9aca3c;
		}
	}
}
