.header {
	position: fixed;
	z-index: 500;
	background-color: #fff;
	left: 0;
	right: 0;
	top: 0;
	background-color: var(--color-e);
	box-shadow: 0 5px 15px rgba(143, 150, 176, 0.3);
	// box-shadow: 0 5px 15px rgba(143, 150, 176, 0.3);
	&--main {

		.header {
			&__wrapper {
				// padding-top: 10px;
				padding-bottom: 0;
				align-items:center;
				
			}
		}

		.item-nav {
			&:hover {
				.Nav-link {
					opacity: 1;
					visibility: visible;
				}
			}
		}

		.Nav-link {
			opacity: 0;
			visibility: hidden;
		}

		&-active {
			background-color: var(--color-e);
			box-shadow: 0 5px 15px rgba(143, 150, 176, 0.3);

			.header {
				&__wrapper {
				}
			}
		}
	}

	&--active {
		background-color: var(--color-e);
		box-shadow: 0 5px 15px rgba(143, 150, 176, 0.3);

		.header {
			&__wrapper {
				transition: 0.3s padding;

				// padding-top: 10px;
				// padding-bottom: 0;
			}
		}
	}

	&--not-nav {
		.header {
			&__nav-link {
				display: none;
			}
		}
	}

	&__action {
		display: flex;
    align-items: center;
    justify-content: center;
	}

	&__logo {
		@include adaptiv-value('margin-right', $offset-3, $offsetMobile-3, 1);

		cursor: pointer;
	}

	&__logo-picture {
		@include adaptiv-value('width', 85, 85, 1);

		.Picture {
			&__block-img {
				padding-bottom: 71%;
			}
		}
	}

	&__navigation {
		display: flex;
		align-items: center;
		height: 100%;
	}

	&__nav-link {
		@include adaptiv-value('margin-top', $offset-2, $offsetMobile-2, 1);
	}

	&__wrapper {
		display: flex;
		justify-content: space-between;
		min-height: 93px;
		align-items: center;
	}
}
